@charset "UTF-8";
/* Файл global/variables.scss */
/* Файл global/global.scss */
html {
  box-sizing: border-box; }

*,
*::after,
*::before {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

/* Файл global/fonts.scss */
/* Fonts */
/* open-sans-300 - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  src: local("Open Sans"), local("OpenSans"), local("Open-Sans"), url("../fonts/open-sans-v20-latin_cyrillic-300.woff2") format("woff2"), url("../fonts/open-sans-v20-latin_cyrillic-300.woff") format("woff");
  /* Modern Browsers */
  font-display: swap; }

/* open-sans-regular - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: local("Open Sans"), local("OpenSans"), local("Open-Sans"), url("../fonts/open-sans-v20-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/open-sans-v20-latin_cyrillic-regular.woff") format("woff");
  /* Modern Browsers */
  font-display: swap; }

/* open-sans-600 - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  src: local("Open Sans"), local("OpenSans"), local("Open-Sans"), url("../fonts/open-sans-v20-latin_cyrillic-600.woff2") format("woff2"), url("../fonts/open-sans-v20-latin_cyrillic-600.woff") format("woff");
  /* Modern Browsers */
  font-display: swap; }

/* open-sans-700 - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: local("Open Sans"), local("OpenSans"), local("Open-Sans"), url("../fonts/open-sans-v20-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/open-sans-v20-latin_cyrillic-700.woff") format("woff");
  /* Modern Browsers */
  font-display: swap; }

/* Файл blocks/page.scss */
.page {
  height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400; }

.page__body {
  margin: 0;
  min-height: 100%;
  color: #000000;
  background-color: #ffffff; }

/* Файл blocks/wrap.scss */
.wrap {
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  max-width: 1440px; }
  @media (max-width: 1023px) {
    .wrap {
      max-width: 768px; } }
  @media (max-width: 767px) {
    .wrap {
      max-width: 320px; } }

/* Файл blocks/container.scss */
.container {
  margin: 0 auto;
  padding: 0 143px 240px;
  width: 1166px; }

/* Header */
/* Файл blocks/page-header.scss */
.page-header {
  background-color: #202020;
  background-image: url("../../img/bike-desktop.jpg");
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../../img/bike-desktop@2x.jpg"); } }

.page-header__block {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto; }

.page-header__logo-wrap {
  margin-bottom: 31px;
  grid-column: 1 / 2;
  grid-row: 2 / 3; }

.page-header__main-nav {
  order: -1;
  grid-column: 1 / -1;
  grid-row: 1 / 2; }

.page-header__slogan {
  grid-column: 1 / 2;
  grid-row: 3 / 4; }

.page-header__slogan h1 {
  margin: 0;
  margin-bottom: 24px;
  color: #ffffff;
  font-weight: 700;
  font-size: 46px;
  line-height: 64px;
  letter-spacing: -0.005em; }

.page-header__slogan p {
  margin: 0;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  letter-spacing: -0.005em; }

.page-header__wrap-form {
  padding-left: 113px;
  grid-column: 2 / -1;
  grid-row: 2 / 4; }

.page-header__wrap-form p {
  margin: 0;
  margin-bottom: 20px;
  color: #ffffff;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  text-align: center;
  letter-spacing: -0.005em; }

/* Файл blocks/main-nav.scss */
.main-nav__toggle {
  display: none; }

.main-nav__wrapper {
  padding: 21px 0 9px; }

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; }

.main-nav__item {
  width: calc(100% / 4); }

/* Файл blocks/form.scss */
.form {
  display: flex;
  flex-direction: column; }

.form__field {
  margin-bottom: 20px;
  display: flex; }

.form__field input {
  padding: 14px 29px 10px;
  flex-grow: 1;
  color: #000000;
  background-color: #ffffff;
  font-family: inherit;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  border: 1px solid #c9ccd4;
  border-radius: 6px; }

.form__field input::placeholder {
  color: rgba(28, 28, 28, 0.5); }

.form__field input:hover::placeholder,
.form__field input:focus::placeholder {
  color: #ff0000; }

.form__button {
  padding: 0;
  border: none;
  background-color: #0ad9c6;
  cursor: pointer;
  height: 48px;
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  color: #000000;
  border-radius: 6px; }

.form__button:hover,
.form__button:focus {
  color: #000000;
  background-color: #826628; }

.form__button:active {
  color: #000000;
  background-color: #f6f7f9; }
